<template>
  <v-layout justify-center>
    <v-flex xs12 sm6 md6 lg5>
      <update-user
        :form="form"
        :select_items="list_enabled"
        :update="false"
        :disable="true"
        v-on:createRegister="createRegister()"
      ></update-user>
    </v-flex>
  </v-layout>
</template>

<script>
import UpdateUser from "@/components/Form/UpdateUser.vue";
import { mapGetters, mapActions } from "vuex";
export default {
  components: {
    UpdateUser
  },
  data() {
    return {
      names: "",
      lastname: "",
      ci: "",
      mobile: "",
      email: "",
      genre: "",
      id: null,
      course_id: null
    };
  },

  computed: {
    ...mapGetters({
      list_enabled: "book/list_enabled"
    }),
    form() {
      return {
        nombres: this.names + " " + this.lastname,
        correo: this.email,
        ci: this.ci,
        cel: this.mobile,
        estudiante_id: this.id,
        curso_id: this.course_id
      };
    }
  },

  methods: {
    ...mapActions({
      enrollStudent: "student/enrollStudent",
      fetchBooks: "book/fetchBooks",
      fetchCourses: "book/fetchCourses"
    }),
    resetForm() {
      Object.keys(this.form).forEach(f => {
        this.$refs[f].reset();
      });
      this.$validator.reset();
    },

    createRegister() {
      this.enrollStudent({ curso_id: this.form.curso_id, estudiante_id: this.form.estudiante_id }).then(() => {
        console.log("Lo registroooo");
      });
    }
  },
  mounted() {
    if (this.$route.params.id) {
      this.fetchBooks().then(() => {
        this.fetchCourses();
      });
      this.names = this.$route.params.nombres;
      this.lastname = this.$route.params.apellidos;
      this.email = this.$route.params.correo;
      this.ci = this.$route.params.ci;
      this.mobile = this.$route.params.cel;
      this.id = this.$route.params.id;
    } else {
      this.$router.push({name: 'search-student'})
    }
  }
};
</script>

